body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.flex {
  display: flex;
}

.flex-center {
  align-items: center;
  justify-content: center;
}

.row {
  flex-direction: row;
}

.column {
  flex-direction: column;
}

.container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.header {
  background-color: #282c34;
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 25px 15px;
}

.header a{
  text-decoration: none;
  color: aliceblue;
}
.header span {
  cursor: pointer;
}

.header span:hover {
  text-decoration: underline;
}


.link{
  justify-content: space-evenly;
  flex: 1 1;
  font-size: 20px;
}

.login-form {
    padding: 40px 0;
}

.btn-wrapper {
    padding: 20px 0;
}

.alert-danger {
    color: red;
    font-weight: 300;
}
.spinner-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(17, 84, 143, 0.27);
}
.not-found{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 70vh;
}

.not-found a {
    color: black;
    text-decoration: underline;
}
.reg-users-container{
    background: white;
    border-radius: 10px;
    padding: 0 10px;
    overflow-x: scroll;
}
.users-nav{
    align-items: center;
    padding: 0 10px;
    font-weight: 300;
}
.users-nav button{
    padding: 5px 8px;
    border: none;
    border-radius: 20px;
    background: whitesmoke;
    margin-left: 20px;
    cursor: pointer;
    color: blueviolet;
    font-weight: 300;
}
table{
    background: whitesmoke;
    margin-bottom: 20px;
    border-collapse: collapse;
    width: 100%;
    overflow-y: auto;
    border-radius: 10px;
}

td, th {
    border-bottom: 1px solid white;
    text-align: left;
    padding: 8px;
}
td{
    font-weight: 300;
}
td a {
    text-decoration: underline;
    color: blueviolet;
}
.edit{
    text-decoration: underline;
    color: green;
    font-weight: 500;
    font-size: 12px;
    cursor: pointer;
}
.delete{
    margin: 5px;
    color: red;
    text-decoration: underline;
    cursor: pointer;
    font-weight: 500;
    font-size: 12px;
}

.info{
    font-weight: 200;
    font-size: 14px;
    justify-content: right;
}
